body{
    background: linear-gradient(45deg, #f4f4f4, #cfebf4);
    margin: 0;
    padding: 0;
}

header{
    background-color: darkblue;
    color: white;
    padding: 1rem;
    margin: 0;
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    font-size: large;
}

nav{
    ul{
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        justify-content: center;
    }
    li{
        display: inline-block;
        margin: 1rem;
        a{
            text-decoration: none;
            color: black;
        }
    }
}
article{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    h3{
        text-align: center;
        border: 1px solid black;
        border-radius: 50px;
        background-color: rgb(0, 0, 255);
        color: white;
        padding: 5px;
        margin: 1rem;
        flex: 1 1 100%;
    }
    section{
        border: 1px solid black;
        border-radius: 5px;
        padding: 1rem;
        margin: 1rem;
        display: grid;
        background-color: #cfebf4;
        width: 400px;
    
        h4{
            text-align: center;
            font-size: 2rem;
            margin-bottom: 1rem;
        }
        img{
            width: 100px;
            margin-bottom: 1rem;
            justify-self: center;
        }
        p{
            margin-bottom: 1rem;
        }
    }
}

footer{
    color: darkblue;
    font-family: 'Courier New', Courier, monospace;
    font-size: large;
    text-align: center;
    padding: 1rem;
    background-color: #f4f4f4;
    border-top: 1px solid darkblue;
}